.form {
  position: relative;
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 15px;
  row-gap: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  padding-bottom: 22px;
  box-sizing: border-box;
  background-color: #d3c1a2;
  outline: 2px solid #ffffff;
  outline-offset: -4px;
}
.form::before {
  content: "";
  position: absolute;
  width: 280px;
  height: 22px;
  background-image: url("../img/decor-up.svg");
  background-size: cover;
  top: -20px;
  left: 0px;
}
.form::after {
  content: "";
  position: absolute;
  width: 280px;
  height: 22px;
  background-image: url("../img/decor-up.svg");
  background-size: cover;
  transform: rotate(180deg);
  bottom: -20px;
  left: 0;
}
@media (min-width: 768px) {
  .form {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0;
    padding-top: 13px;
    padding-bottom: 13px;
    flex-wrap: wrap;
  }
  .form::before,
  .form::after {
    left: 50%;
  }
  .form::before {
    transform: translateX(-50%);
  }
  .form::after {
    transform: translateX(-50%) rotate(180deg);
  }
}
@media (min-width: 1440px) {
  .form {
    width: 1043px;
    padding-right: 200px;
    padding-left: 86px;
  }
  .form::before,
  .form::after {
    width: 400px;
    height: 34.6px;
  }
  .form::before {
    top: -30px;
    left: 422px;
  }
  .form::after {
    bottom: -30px;
    left: 422px;
  }
}
@media (min-width: 1920px) {
  .form {
    left: 30px;
    padding-left: 58px;
    padding-right: 425px;
    width: 1400px;
  }
  .form::before {
    content: "";
    position: absolute;
    width: 508px;
